@import url(https://fonts.googleapis.com/css?family=Sofia&effect=neon|fire|emboss|shadow-multiple);
.header {
    display: flex;
    justify-content: space-between; 
    height: 150px;
    background-color: rgb(35, 88, 200);
  }
  
  .content {
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* This will push the h1 to the top and navTabs to the bottom */
    height: 100%; /* This is necessary to ensure flex items are spread across the full height */
  }
  
  .navTabs {
    display: flex;
    justify-content: space-between;
    list-style: none;
    align-items: baseline;
  }
  
  .pageBtn {
    display: flex;
    align-items: baseline;
    background-color: rgb(234, 169, 18);
    padding: 5px;
    border-radius: 90px;;
    border: black 2px solid;
    font-weight: bolder;
    box-shadow: 15px 15px 15px rgba(0, 0, 0, 0.65);
    margin-bottom: 5px;
    color: rgb(255, 255, 255);
  }
  
  .image {
    height: 150px;
    margin-right: 20px;
    margin-top: 20px;
    margin-left: 5px;
    box-shadow: 30px 30px 30px rgba(0, 0, 0, 0.65);
    border-radius: 20%;
  }
  
  .myName {
    margin-left: 50px;
  }

  #floating-text {
    margin-top: 10px;
    position: absolute;
    -webkit-animation: float 7s infinite linear;
            animation: float 7s infinite linear;
    font-weight: bolder;
  }
  
  @-webkit-keyframes float {
    0% {
      -webkit-transform: translate3d(0, 0px, 0);
              transform: translate3d(0, 0px, 0);
    }
    25% {
      -webkit-transform: translate3d(70px, 70px, 0);
              transform: translate3d(70px, 70px, 0);
    }
    50% {
      -webkit-transform: translate3d(0, 100px, 0);
              transform: translate3d(0, 100px, 0);
    }
    75% {
      -webkit-transform: translate3d(70px, 100px, 0);
              transform: translate3d(70px, 100px, 0);
    }
    100% {
      -webkit-transform: translate3d(0, 0, 70px);
              transform: translate3d(0, 0, 70px);
    }
  }
  
  @keyframes float {
    0% {
      -webkit-transform: translate3d(0, 0px, 0);
              transform: translate3d(0, 0px, 0);
    }
    25% {
      -webkit-transform: translate3d(70px, 70px, 0);
              transform: translate3d(70px, 70px, 0);
    }
    50% {
      -webkit-transform: translate3d(0, 100px, 0);
              transform: translate3d(0, 100px, 0);
    }
    75% {
      -webkit-transform: translate3d(70px, 100px, 0);
              transform: translate3d(70px, 100px, 0);
    }
    100% {
      -webkit-transform: translate3d(0, 0, 70px);
              transform: translate3d(0, 0, 70px);
    }
  }
  
  
  @media (max-width: 768px) {
    #floating-text {
      display: none;
    }
  }

  
