.contact {
    height: 100vh;
    margin-top: 100px
}
.contactForm {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border: 5px solid #000;
    padding: 20px;
    width: 800px;
    margin: 0 auto;
    height: 600px;
    border-radius: 16%;
    box-shadow: 15px 15px 15px rgba(0, 0, 0, 0.65);
    background-color: rgb(99, 192, 246);

}

.contactForm label {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-bottom: 15px;
}

.contactForm input, .contactForm textarea {
    margin-top: 5px;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}

.textArea {
    resize: vertical; 
}

.contactForm input[type="submit"] {
    background-color: rgb(234, 169, 18);
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s ease;
    border-radius: 90px;
}

.contactForm input[type="submit"]:hover {
    background-color: #4CAF50;
}

@media (max-width: 768px) {
    .contactForm {
        width: 90%;
    }
}

